import Mortgage from './mortgage.js'

document.getElementById('calcBtn').addEventListener('click',  () => {
    let principal = document.getElementById("principal").value;
    let years = document.getElementById("years").value;
    let rate = document.getElementById("rate").value;
    let mortgage = new Mortgage(principal,years,rate);
    document.getElementById("monthlyPayment").innerHTML = mortgage.monthlyPayment.toFixed(2);
    document.getElementById("monthlyRate").innerHTML = (mortgage.monthlyRate*100).toFixed(2);
    let html = "";
    mortgage.amortization.forEach((year, index) => html += `
            <tr>
            <td>${index + 1}</td>
            <td class="currency">${Math.round(year.principalY)}</td>
            <td class="stretch">
                <div class="flex">
                    <div class="bar principal"
                         style="flex:${year.principalY};-webkit-flex:${year.principalY}">
                    </div>
                    <div class="bar interest"
                         style="flex:${year.interestY};-webkit-flex:${year.interestY}">
                    </div>
                </div>
            </td>
            <td class="currency left">${Math.round(year.interestY)}</td>
            <td class="currency">${Math.round(year.balance)}</td>
        </tr>
        `
    );
    let x = document.createElement("TABLE");
    x.innerHTML = html;
    // console.log(html);
    document.getElementById("amortization").appendChild(x);
});


